@import "../../../style/theme/default/var.css";

@component-namespace institution{
  @b form{
    @e cut{
      border-bottom:1px solid var(--theme-border-color-primary);
      margin-bottom:var(--theme-margin-small);
    }
  }

  @b rating{
    & >  .el-form-item__content{
      margin-left:0 !important;
    }

    & .el-form-item {
/*        margin-bottom: 10px;*/
      }

    @e property{
      padding: var(--theme-padding-base) 0 var(--theme-padding-small);
    }
    @e tabs{
      margin-bottom:var(--theme-margin-base);
      margin-top:-1px;
      &.el-tabs--border-card{
         box-shadow:none;
          & > .el-tabs__content{
            padding: 0 0;
          }
       }

        & .el-tabs__header{
        display: none;
        }
        &  .el-tabs__item {
             padding:0 var(--theme-padding-base);
             vertical-align: middle;
           }
        .el-tabs__nav-custom{
          display: inline-block;
          vertical-align: middle;
        }
        .tmp-toggles {
          padding-right: var(--theme-padding-base);
        }

      }

   }

}
.tmp-popover-add{
  .el-checkbox{
    width:100%;
    height: 18px;
  @utils-ellipsis;
  }
  .el-checkbox:first-child{
    margin-left: var(--theme-margin-base);
  }
  .el-checkbox + .el-checkbox{
    margin-left: var(--theme-margin-base);
  }
  .order-popover-footer{
    padding-top:5px;
    text-align: right;
  }
  .el-checkbox__label {
    font-size: var(--theme-font-size-small);
  }
}
.tmp-popover-add-footer{
  padding-top: 5px;
  text-align: right;
}

/*    拖拽结构*/
@component-namespace rating{
  @b drag {
    background-color: var(--theme-background-color-primary);
    border:1px solid  var(--theme-border-color-primary);
    @e bar{
      margin-bottom: -1px;
      .rating-header{
        display: inline-block;
        & .rating-header__item,
        & .el-dropdown{
            display:inline-block;
            transition: all .3s cubic-bezier(.645,.045,.355,1);
            border: 1px solid transparent;
            border-top: 0;
            margin-left: -1px;
            padding: 0 10px;
            height:36px;
            line-height:36px;

          &.is-active {
             background-color: var(--color-white);
             border-right-color: var(--theme-border-color-primary);
             border-left-color: var(--theme-border-color-primary);
             color: var(--theme-color-primary);
          }
          &:hover{
              cursor: pointer;
              label {
                cursor: pointer;
              }
              label.is-move {
                cursor: move;
              }
           }

          & .rating-name label{
            padding-right: 4px;
          }

          & i{
              font-size:var(--theme-font-size-small);
              transform: scale(0.8,0.8);
            &:hover{
               color: tint(var(--color-primary), var(--button-hover-tint-percent));
             }
           }
        }
      }

      .rating-input{
        margin-left: 6px;
        margin-top: 3px;
        & .el-button {
          height: 30px;
          border:1px dotted var(--theme-color-primary);
        }
        i {
          cursor: pointer;
          margin: 0 var(--theme-margin-small) 0 0;
          &.check-btn {
             font-size: 21px;
             color: var(--theme-color-primary);
            &:hover {
               color: darken(var(--theme-color-primary), 15%);
             }
          }
          &.close-btn {
             font-size: 21px;
             color: var(--theme-font-color-primary);
            &:hover {
               color: darken(var(--theme-font-color-primary), 15%);
             }
          }
        }
      }

    }
  }

}

/*  /end  拖拽结构*/



